import { toast } from "react-toastify";
import { Link } from "react-router-dom";
import Invoice from "../components/Invoice";
import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { listProducts } from "../actions/productAction";
import { myOrders, myOrdersDelete } from "../actions/orderAction";
import {
  Document,
  Page,
  PDFDownloadLink,
  Text,
  View,
} from "@react-pdf/renderer";
import { createReport, ReportsList } from "../actions/ReportAction";
import Buttons from "../components/UI/Buttons";
import { BiDownload } from "react-icons/bi";
import { FiDownload } from "react-icons/fi";
import SEO from "../components/SEO";

const MyOrders = () => {
  const { loading, error, orders } = useSelector((state) => state.myOrder);
  const {
    loading: myorderDeleteLoading,
    error: myorderDeleteError,
    success: myorderDeleteSuccess,
  } = useSelector((state) => state.myOrderDelete);
  const user = useSelector((state) => state.user);
  const {
    error: errorReport,
    loading: loadingError,
    success: successReport,
  } = useSelector((state) => state.reportCreate);
  const {
    loading: ReportListLoading,
    reports,
    error: ReportlistError,
  } = useSelector((state) => state.reportList);

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(myOrders());
    dispatch(listProducts());
    dispatch(ReportsList());
  }, [dispatch, user, myorderDeleteSuccess, successReport]);

  let isReportConfirmed;

  const DeleteHandler = (id) => {
    if (window.confirm("This may help you in future")) {
      dispatch(myOrdersDelete(id));
    }
  };

  const showOrderInTable = (order) => {
    const days = new Date(new Date() - new Date(order.createdAt)).getDate() - 1;
    const Returnable = days <= 7 && order.isDelivered ? true : false;
    const cancelable = !order.isDelivered && true;
    const namedOfVar = order.reported
      ? "Reported"
      : Returnable
      ? "Return"
      : cancelable
      ? "Cancel"
      : "";
    const submitHandling = () => {
      if (window.confirm("Are You Sure")) {
        dispatch(createReport(report));
      }
    };

    const report = {
      userId: order.user,
      orderId: order._id,
      createdAt: order.createdAt,
      isDelivered: order.isDelivered,
      alreadyReported: true,
      typeReport: namedOfVar,
    };

    isReportConfirmed =
      !ReportListLoading && reports.find((x) => x.orderId === order._id);

    return (
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200 text-sm">
          <thead className="bg-gray-100">
            <tr>
              <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                ID
              </th>
              <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                Total
              </th>
              <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                Tax Price
              </th>
              <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                Date
              </th>
              <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                Items
              </th>

              {order.orderItems.map((p) => (
                <th
                  className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900"
                  key={p._id}
                >
                  Products
                </th>
              ))}
            </tr>
          </thead>

          <tbody className="divide-y divide-gray-200">
            <tr>
              <td className="whitespace-nowrap px-4 py-2 font-medium text-gray-900">
                <b>{order._id}</b>
              </td>
              <td className="whitespace-nowrap px-4 py-2 text-gray-700">
                {order.totalPrice}
              </td>
              <td className="whitespace-nowrap px-4 py-2 text-gray-700">
                {order.taxPrice}
              </td>
              <td className="whitespace-nowrap px-4 py-2 text-gray-700">
                {order.createdAt}
              </td>
              <td className="whitespace-nowrap px-4 py-2 text-gray-700">
                {order.orderItems.length}{" "}
              </td>
              {order.orderItems.map((p) => (
                <td>{p.name} </td>
              ))}
            </tr>

            <thead classNameName="thead-light">
              <tr>
                <th scope="col">ID</th>
                <th scope="col">Total</th>
                <th scope="col">Tax Price</th>
                <th scope="col">Date</th>
                <th scope="col">Items</th>

                {order.orderItems.map((p) => (
                  <th scope="col" key={p._id}>
                    Products
                  </th>
                ))}
              </tr>
            </thead>

            <tbody>
              <tr>
                <td>
                  <b>{order._id}</b>
                </td>
                <td>{order.totalPrice}</td>
                <td>{order.taxPrice}</td>
                <td>{order.createdAt}</td>
                <td>{order.orderItems.length} </td>
                {order.orderItems.map((p) => (
                  <td>{p.name} </td>
                ))}
              </tr>
            </tbody>
            <button
              classNameName="btn btn-sm btn-block btn-outline-primary mt-2"
              onClick={() => submitHandling(reports)}
              disabled={isReportConfirmed && true}
            >
              <Link>{namedOfVar}</Link> {loadingError && <p>Loading....</p>}
            </button>

            {isReportConfirmed && (
              <strong classNameName="text-dark">
                {" "}
                {!isReportConfirmed.confirmation
                  ? "Your Report is not confirm yet"
                  : "Report has been confrimed, check your email"}{" "}
              </strong>
            )}
            {isReportConfirmed && isReportConfirmed.confirmation && (
              <button
                classNameName="btn btn-sm btn-block btn-outline-danger mt-2"
                onClick={() => DeleteHandler(order._id)}
              >
                Delete
              </button>
            )}
          </tbody>
        </table>
      </div>
    );
  };

  const showDownloadLink = (order) => (
    <PDFDownloadLink
      document={<Invoice order={order} />}
      fileName="invoice.pdf"
      className="group relative inline-flex items-center overflow-hidden rounded bg-indigo-600 px-8 py-3 text-white focus:outline-none focus:ring active:bg-indigo-500"
    >
      <span class="absolute left-0 -translate-x-full transition-transform group-hover:translate-x-4">
        <FiDownload />
      </span>
      <span class="text-sm font-medium transition-all group-hover:ml-4">
        Download
      </span>
    </PDFDownloadLink>
  );

  const showEachOrders = () =>
    orders.length === 0 ? (
      <p>No ordered yet</p>
    ) : (
      orders.map((order, i) => (
        <div key={i} className="container mx-auto">
          {showOrderInTable(order)}
          <div className="text-center">{showDownloadLink(order)}</div>
        </div>
      ))
    );

  return (
    <>
     <SEO title={"Orders"} />
      <header aria-label="Page Header" className="bg-gray-50">
        <div className="mx-auto max-w-screen-xl px-4 py-8 sm:px-6 lg:px-8">
          <div className="mt-8 pb-10">
            <h1 className="text-2xl font-bold text-gray-900 sm:text-3xl">
              All your orders
            </h1>

            <p className="mt-1.5 text-sm text-gray-500">
              Search Products, Delete Products, Edit Products Keep it up! ðŸš€
            </p>
          </div>
        </div>
      </header>

      {errorReport && toast.error(errorReport)}

      <div classNameName="row">
        <div classNameName="col text-center">
          {errorReport && <h4>{errorReport}</h4>}
          {loading ? (
            <p>loading...</p>
          ) : error ? (
            toast.warning("Wait...")
          ) : (
            showEachOrders()
          )}
        </div>
      </div>
    </>
  );
};

export default MyOrders;
